.convertConteiner {
  margin:auto;
  max-width: 700px;
  display: flex;
  gap: 25px;
  background-color: gray;
  padding: 25px;
  border-radius: 10px;
}

.convertItem {
  display: flex;
}

.convertItem input,.convertItem select {
  font-family: 'Courier New', Courier, monospace;
  font-weight: 500;
  font-size: 15px;
}

.convertSwap {
  background: none;
  outline: none;
  border: none;
  padding: 0;
  cursor: pointer;
}

.convertSwap:hover path {
  fill: rgb(48, 47, 47);
}